
import { Component, OnInit, Input, forwardRef, EventEmitter, Output } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, FormControl } from '@angular/forms';
import { DateAdapter, MAT_DATE_FORMATS } from '@angular/material/core';
import { MomentDateAdapter } from '@angular/material-moment-adapter';
import * as moment_ from 'moment';
const moment = moment_;

export const MY_FORMATS = {
    parse: {
        dateInput: 'LL',
    },
    display: {
        dateInput: 'LL',
        monthYearLabel: 'MMM YYYY',
        dateA11yLabel: 'LL',
        monthYearA11yLabel: 'MMMM YYYY',
    },
};

@Component({
    selector: 'ngx-date-time-picker',
    template: `<div>
  <div class="row">
    <div [class]="getDatePickerCssClass()">
      <div class="input-group">
        <input matInput 
              [matDatepicker]="picker" 
              class="form-control" 
              [value]="value" 
              placeholder="Choose a date" 
              (dateChange)="onDateSelect($event)"
              (click)="picker.open()"
              readonly
        >
        <mat-datepicker #picker disabled="false"></mat-datepicker>
        <mat-datepicker-toggle matSuffix [for]="picker" class="input-group-btn"></mat-datepicker-toggle>
      </div>
    </div>
    <div [class]="getWeekPickerCssClass()" *ngIf="showWeeks">
      <mat-select placeholder="Select Weeks" class="form-control" name="weeks" [value]="modelValue" (ngModelChange)="weekSelect($event)">
        <mat-option *ngFor="let count of weeks" [value]="count">
          {{count}} Weeks
        </mat-option>
      </mat-select>
    </div>
    <div [class]="getTimePickerCssClass()" *ngIf="showTime">
      <div class="input-group">
        <input type="time" class="form-control" atp-time-picker [value]="selectedTime" (ngModelChange)="onTimeSelect($event)"
          id="ngx-atp-time-picker" />
        <div class="input-group-btn">
          <button mat-raised-button class="btn btn-default" (click)="setCurrentTime()">
            Set Current Time
          </button>
        </div>
      </div>
    </div>
  </div>
</div>`,
    styles: [`#time-section{display:inline-block}#ngx-atp-time-picker,#ngx-mat-form-field{width:100%}.up{bottom:100%!important;top:auto!important}.time-btn{margin-top:-20px}`],
    providers: [
        { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
        { provide: DateAdapter, useClass: MomentDateAdapter },
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => NgxDateTimePickerComponent),
            multi: true
        }
    ]
})
export class NgxDateTimePickerComponent implements OnInit, ControlValueAccessor {

    // public date = new FormControl(moment());
    public selectedTime = moment().format('HH:mm');
    public selectedDate = moment().format();
    public loadInitial = false;
    public value;
    @Input() weeks: number[] = [0, 2, 4, 6, 8, 12, 16, 24];
    @Input() modelValue: any;
    @Input() showTime = false;
    @Input() showWeeks = true;
    @Output() onDateChange = new EventEmitter<any>();
    public onChange: any = () => { };
    public onTouched: any = () => { };
    public ngOnInit() {

    }


    public writeValue(value) {
        console.log('Write Value', value);
        this.value = value;
    }

    public registerOnChange(fn) {
        this.onChange = fn;
    }

    public registerOnTouched(fn) {

    }

    public onTimeSelect($event) {
        const setDate = moment(this.selectedDate);
        const setTime = $event;
        this.setDateTime(setDate, setTime);
    }

    public onDateSelect($event) {

        const setDate = moment($event.value);
        const setTime = this.selectedTime;
        const dateString = this.setDateTime(setDate, setTime);

        const selectedDate = $event.value;
        // const dateString = moment(selectedDate).format();
        // const dateString = this.processDateValue(selectedDate);
        this.value = dateString;
        // this.onDateChange.emit(dateString);

        console.log('On Date Select', selectedDate);
        console.log('On Date Select', dateString);

    }


    public setCurrentTime() {

        const setDate = moment(this.selectedDate);
        const currentTime = moment().format('HH:mm:ss');
        this.setDateTime(setDate, currentTime);
    }

    public weekSelect($event) {
        const dateToUse = moment().format();
        const nextWeekDate = moment(dateToUse).add($event, 'weeks');
        const nextWeekTime = dateToUse;
        this.setDateTime(nextWeekDate, nextWeekTime);
    }


    public getWeekPickerCssClass() {
        if (this.showTime) {
            return 'col-sm-2 form-group';
        }
        return 'col-sm-3 form-group';
    }

    public getDatePickerCssClass() {
        if (this.showTime && this.showWeeks) {
            return 'col-sm-5 form-group';
        }

        if (this.showWeeks === true) {
            return 'col-sm-9 form-group';
        }

        if (this.showTime === true) {
            return 'col-sm-8 form-group';
        }
        return 'col-sm-12 form-group';
    }

    public getTimePickerCssClass() {
        if (this.showTime && this.showWeeks) {
            return 'col-sm-5 form-group';
        }

        if (this.showWeeks === true) {
            return 'col-sm-9 form-group';
        }
        return 'col-sm-4 form-group';
    }

    public setDateTime(setDate, setTime) {
        const newDate = moment(setDate).format('DD-MM-YYYY');
        let newTime;
        if (this.showTime) {
            newTime = setTime;
        } else {
            newTime = '00:00:00';
        }
        console.log('Show Time', this.showTime);
        console.log('New Time', newTime);
        const newDateTime = moment(newDate + '' + newTime, 'DD-MM-YYYY HH:mm:ss');
        const dateTimeString = moment(newDateTime).format();
        this.selectedDate = dateTimeString;
        this.selectedTime = newTime;
        this.value = dateTimeString;
        this.onChange(this.value);

        return dateTimeString;


    }
}
